@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -20%, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

/*fade in up*/
@keyframes fadeup {
  0% {
    transform: translateY(30px);
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes header_off1 {
  0% {
    left: -10%;
    opacity: 0;
  }
  15%,
  85% {
    left: 0;
    opacity: 1;
  }
  100% {
    left: 100%;
    opacity: 0;
  }
}

@keyframes play {
  0% {
    transform: scale(1);
  }
  15% {
    box-shadow: 0 0 0 2px rgba(154, 154, 154, 0.4);
  }
  25% {
    box-shadow: 0 0 0 3px rgba(154, 154, 154, 0.4), 0 0 0 4px rgba(154, 154, 154, 0.2);
  }
  25% {
    box-shadow: 0 0 0 4px rgba(154, 154, 154, 0.4), 0 0 0 4px rgba(154, 154, 154, 0.2);
  }
}

@keyframes jump {
  50% {
    transform: translateY(-13px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes rotation {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes titlerun {
  0% {
    left: -100%;
  }
  40% {
    left: 0;
  }
  60% {
    left: 0;
  }
  100% {
    left: 100%;
  }
}

@keyframes titlerunNews {
  0% {
    top: -100%;
  }
  40% {
    top: 0;
  }
  60% {
    top: 0;
  }
  100% {
    top: 100%;
  }
}

@keyframes lineNav {
  0% {
    left: -100%;
    opacity: 0;
  }
  15%,
  85% {
    left: 0;
    opacity: 1;
  }
  100% {
    left: 100%;
    opacity: 0;
  }
}

@keyframes slideInDown {
  from {
    -webkit-transform: translate3d(0, -10%, 0);
    transform: translate3d(0, -10%, 0);
    visibility: visible;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.slideInDown {
  -webkit-animation-name: slideInDown;
  animation-name: slideInDown;
}

@keyframes slideInUp {
  from {
    -webkit-transform: translate3d(0, 10%, 0);
    transform: translate3d(0, 10%, 0);
    visibility: visible;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.slideInUp {
  -webkit-animation-name: slideInUp;
  animation-name: slideInUp;
}

.work_box-info-col:nth-child(2) .img-big {
  transition: 0.5s all;
}

.work_box-info-col:nth-child(2) .img-big.showElement {
  -webkit-animation-name: slideInDown;
  animation-name: slideInDown;
  opacity: 1;
}

.work_box-info-col:nth-child(2) .img-small {
  transition: 0.5s all;
}

.work_box-info-col:nth-child(2) .img-small.showElement {
  -webkit-animation-name: slideInUp;
  animation-name: slideInUp;
  opacity: 1;
}

.lineTwoLeft01 {
  position: relative;
}

.lineTwoLeft01:before {
  background-color: #000;
  height: 100%;
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  animation: titlerun ease-in-out 1s forwards;
}

@keyframes zoomIn {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }
  50% {
    opacity: 1;
  }
}

.zoomIn {
  -webkit-animation-name: zoomIn;
  animation-name: zoomIn;
}

@keyframes fadeInTopRight {
  from {
    opacity: 0;
    -webkit-transform: translate3d(10%, -10%, 0);
    transform: translate3d(10%, -10%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.animate__fadeInTopRight {
  -webkit-animation-name: fadeInTopRight;
  animation-name: fadeInTopRight;
}

@keyframes fadeInTopLeft {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-10%, -10%, 0);
    transform: translate3d(-10%, -10%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.animate__fadeInTopLeft {
  -webkit-animation-name: fadeInTopLeft;
  animation-name: fadeInTopLeft;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fade-in {
  animation: fadeIn ease 2s;
  -webkit-animation: fadeIn ease 2s;
  -moz-animation: fadeIn ease 2s;
  -o-animation: fadeIn ease 2s;
  -ms-animation: fadeIn ease 2s;
}

.mv-main {
  animation: fadeIn ease .5s;
  overflow: hidden;
}

