  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -10%, 0);
    transform: translate3d(0, -100%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.animate__fadeInDown {
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
}

.list-img.images-center {
  opacity: 0;
  transform: scale(0.99);
  transition: all 0.8s ease-out;
}

.list-img.images-center.active {
  opacity: 1;
  transform: scale(1);
}

.ag-format-container {
  transform: scale(0.99);
  opacity: 0;
  position: absolute;
  transition: all 0.9s ease-in-out;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.ag-format-container.active {
  transform: scale(1, 1);
  opacity: 1;
}

.ag-glitch_item {
  list-style: none;
  background-color: transparent;
  background-blend-mode: normal;
  background: url(../images/network_map-rotage.png) no-repeat center;
  background-size: cover;
  position: absolute;
  top: 1%;
  left: 0%;
  right: 0%;
  bottom: 5%;
  transform: translate3d(0, 0, 0);
  width: 42.19vw;
  height: 40vw;
}

@media (min-width: 768px) and (max-width: 1023px) {
  .ag-glitch_item {
    width: 100%;
    height: 43vw;
    margin: auto;
    top: 0;
  }
}

@media (max-width: 767px) {
  .ag-glitch_item {
    width: 90%;
    height: 70vw;
    margin-left: 5vw;
  }
}

.ag-glitch_item:nth-child(n+2) {
  opacity: 0;
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

.ag-glitch_item:nth-child(2) {
  background-color: transparent;
  background-blend-mode: normal;
  -webkit-animation-name: glitch-anim-1;
  animation-name: glitch-anim-1;
}

.ag-glitch_item:nth-child(3) {
  background-color: transparent;
  background-blend-mode: normal;
  -webkit-animation-name: glitch-anim-2;
  animation-name: glitch-anim-2;
}

.ag-glitch_item:nth-child(4) {
  background-color: transparent;
  background-blend-mode: overlay;
  -webkit-animation-name: glitch-anim-3;
  animation-name: glitch-anim-3;
}

.ag-glitch_item:nth-child(5) {
  background-color: transparent;
  background-blend-mode: overlay;
  -webkit-animation-name: glitch-anim-flash;
  animation-name: glitch-anim-flash;
}

