@media (max-width: 768px) {
  .menu-content {
    transform: translateY(-100%);
    -webkit-backdrop-filter: brightness(2) blur(4px);
    backdrop-filter: brightness(2) blur(4px);
    transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1) 0s;
    background: #fff;
    width: 100%;
    height: 55vh;
    position: absolute;
  }
  .menu-content.active {
    transform: translateY(0);
  }
  .header-item .sp-line {
    display: block;
  }
}

@media (max-width: 768px) {
  .sp-line {
    position: absolute;
    right: 20px;
    top: 30px;
  }
  .sp-line div {
    transition-timing-function: ease-in-out;
    left: 0;
  }
  .sp-line div:nth-child(1) span {
    transition: 0.3s all;
    width: 50px;
    height: 1px;
    background: #000;
    margin-bottom: 8px;
    animation: lineNav 2.5s ease-in-out 0s infinite;
  }
  .sp-line div:nth-child(2) span {
    transition: 0.3s all;
    width: 40px;
    height: 1px;
    background: #000;
    text-align: right;
    margin-left: auto;
    animation: lineNav 2.5s ease-in-out .15s infinite;
  }
  .sp-line span {
    display: block;
  }
  .sp-line.active div:nth-child(1) span {
    width: 30px;
    transform: rotate(45deg) translate(8px, 8px);
  }
  .sp-line.active div:nth-child(2) span {
    width: 30px;
    transform: rotate(-45deg) translate(-2px, 2px);
  }
}

